import React, { useEffect } from "react";
import { Button, View } from "react-native-ui-lib";
import { Alert } from "react-native";
import { useAuth } from "../contexts/AuthContext";
import * as Keychain from 'react-native-keychain';
import AsyncStorage from "@react-native-async-storage/async-storage";
import { storeFlag, storeUsernameAndPass } from "../contanst";

const AccountScreen = (props: any) => {
  const { navigation, route } = props;
  const { signOut } = useAuth()

  useEffect(() => {
    console.log(route);

  }, [])

  const handleSignOut = () => {
    signOut()
  }

  const handleFaceID = async () => {
    try {
      const value = await AsyncStorage.setItem(storeFlag, 'true');
      Alert.alert("Đăng kí FaceID thành công");
    } catch (error) {
      // Xử lý lỗi nếu có
      console.error('Error accessing AsyncStorage:', error);
      Alert.alert("Đăng kí FaceID không thành công");
    }
  }

 
  return (
    <View style={{ flex: 1 }} center>
      <Button label='logout' onPress={handleSignOut} />
      <Button label='FaceID' onPress={handleFaceID} />

    </View>
  )
}

export default AccountScreen;